.blog-main {
  height: 100vh;
  padding: 20px;
  background: #fff;
  overflow-y: auto;
}
.blog-main .main-container {
  width: 100%;
  display: flex;
  padding-bottom: 20px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.blog-main .main-container .blog-item {
  flex: 1 1;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  margin-left: 20px;
  padding: 15px;
  cursor: pointer;
  position: relative;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.blog-main .main-container .blog-item label {
  width: 18px;
  font-size: 18px;
  display: inline-block;
  line-height: 1.2;
}
.blog-main .main-container .blog-item span {
  position: absolute;
  font-size: 48px;
  color: #f0f0f0;
  font-style: italic;
  top: 50%;
  right: 30px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.blog-main .main-container .blog-item:first-child {
  margin-left: 0;
}
.blog-main .main-container .blog-item.blog-item-0.active,
.blog-main .main-container .blog-item.blog-item-0:not(.active):hover {
  background: rgba(64, 158, 255, 0.5);
  color: #fff;
  box-shadow: 0 0 10px rgba(64, 158, 255, 0.1);
}
.blog-main .main-container .blog-item.blog-item-1.active,
.blog-main .main-container .blog-item.blog-item-1:not(.active):hover {
  background: rgba(245, 108, 108, 0.5);
  color: #fff;
  box-shadow: 0 0 10px rgba(245, 108, 108, 0.1);
}
.blog-main .main-container .blog-item.blog-item-2.active,
.blog-main .main-container .blog-item.blog-item-2:not(.active):hover {
  background: rgba(230, 162, 60, 0.5);
  color: #fff;
  box-shadow: 0 0 10px rgba(230, 162, 60, 0.1);
}
.blog-main .main-container .blog-item.blog-item-3.active,
.blog-main .main-container .blog-item.blog-item-3:not(.active):hover {
  background: rgba(144, 147, 153, 0.5);
  color: #fff;
  box-shadow: 0 0 10px rgba(144, 147, 153, 0.1);
}
.blog-main .main-container .blog-item.blog-item-4.active,
.blog-main .main-container .blog-item.blog-item-4:not(.active):hover {
  background: rgba(103, 194, 58, 0.5);
  color: #fff;
  box-shadow: 0 0 10px rgba(103, 194, 58, 0.1);
}
.blog-main .main-container .blog-item.blog-item-5.active,
.blog-main .main-container .blog-item.blog-item-5:not(.active):hover {
  background: rgba(255, 140, 0, 0.5);
  color: #fff;
  box-shadow: 0 0 10px rgba(255, 140, 0, 0.1);
}
.blog-main .main-container .blog-item.blog-item-0:not(.active):hover span,
.blog-main .main-container .blog-item.blog-item-1:not(.active):hover span,
.blog-main .main-container .blog-item.blog-item-2:not(.active):hover span,
.blog-main .main-container .blog-item.blog-item-3:not(.active):hover span,
.blog-main .main-container .blog-item.blog-item-4:not(.active):hover span,
.blog-main .main-container .blog-item.blog-item-5:not(.active):hover span {
  color: hsla(0, 0%, 100%, 0.2);
}
.blog-main .container-left {
  width: 83.34%;
}
.blog-main .container-left .blog-list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
}
.blog-main .container-left .blog-list .item {
  width: 33.33%;
  page-break-inside: avoid;
  -webkit-column-break-inside: avoid;
     -moz-column-break-inside: avoid;
          break-inside: avoid;
  background: #fff;
  margin-bottom: 20px;
  min-height: 150px;
  padding-right: 20px;
  flex-grow: 0;
  position: relative;
}
.blog-main .container-left .blog-list .item a {
  display: block;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  width: 100%;
  color: #333;
  text-decoration: none;
}
.blog-main .container-left .blog-list .item a img {
  width: 100%;
  display: block;
  height: 200px;
  object-fit: cover;
}
.blog-main .container-left .blog-list .item a .item-info {
  padding: 10px;
}
.blog-main .container-left .blog-list .item a .item-info h4 {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.2;
}
.blog-main .container-left .blog-list .item a .item-info .value-line {
  font-size: 12px;
  font-family: "\5B8B\4F53";
  color: #ccc;
  display: flex;
  justify-content: space-between;
}
.blog-main .container-left .blog-list .item a .item-info .value-line span {
  letter-spacing: 0.2px;
}
.blog-main .container-left .blog-list .item:nth-child(n) {
  -webkit-animation-delay: 0.04s;
          animation-delay: 0.04s;
}
.blog-main .container-right {
  width: 16.66%;
  padding-left: 20px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.blog-main .container-right .ant-tag {
  margin-bottom: 8px;
  cursor: pointer;
}
.blog-main .example {
  text-align: center;
  border-radius: 4px;
  margin-bottom: 20px;
  padding: 30px 50px;
  margin: 20px 0;
  color: #ccc;
  font-size: 16px;
  letter-spacing: 0.5px;
}
