.main {
  height: 100%;
  position: relative;
}
.main .music-container {
  color: #fff;
  font-size: 12px;
  font-family: "\5B8B\4F53";
  position: absolute;
  bottom: 20px;
  line-height: 60px;
  right: 280px;
  display: flex;
  border-radius: 8px;
  background: rgba(0, 0, 0, 0.3);
  padding: 0 20px;
  opacity: 0.7;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.main .music-container .music-info {
  padding-right: 20px;
}
.main .ctrl-tools .antIcon-icon:hover,
.main .music-container:hover {
  opacity: 1;
  background: rgba(0, 0, 0, 0.4);
}
.main .ctrl-tools {
  position: absolute;
  bottom: 0;
  right: 0;
  color: #fff;
  padding: 20px;
  padding-left: 20px;
  padding-left: 0;
  display: flex;
}
.main .ctrl-tools .antIcon-icon {
  position: relative;
  display: block;
  width: 60px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  font-size: 28px;
  margin-left: 5px;
  border-radius: 8px;
  background: rgba(0, 0, 0, 0.3);
  opacity: 0.7;
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
  cursor: pointer;
}
.main .ctrl-tools .volume-container {
  display: flex;
  justify-content: center;
  flex-direction: column-reverse;
  align-items: center;
  width: 60px;
  position: absolute;
  bottom: 85px;
  right: 20px;
  border-radius: 8px;
  background: rgba(0, 0, 0, 0.3);
  opacity: 0.7;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  cursor: pointer;
}
.main .ctrl-tools .volume-container span {
  font-size: 12px;
  line-height: 30px;
}
.main .ctrl-tools .volume-container .ant-slider-vertical {
  display: none;
  height: 100px;
  margin-top: 20px;
}
.main .ctrl-tools .volume-container:hover .ant-slider-vertical {
  display: block;
}
