.aside {
  position: relative;
  width: 240px;
  height: 100vh;
  color: white;
  font-size: 16px;
  padding-top: 100px;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  background-color: rgba(0, 0, 0, 0.6);
}
.aside .aside-wrap {
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
}
.aside .aside-wrap img {
  max-width: 100%;
  border-radius: 50%;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}
.aside .aside-wrap p {
  padding-top: 10px;
  font-size: 26px;
  margin: 0;
}
.aside .nav-wrap {
  padding-top: 20px;
}
.aside .nav-wrap .nav-item {
  display: flex;
  align-items: center;
  height: 60px;
  line-height: 60px;
  padding-left: 70px;
  color: white;
  cursor: pointer;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.aside .nav-wrap .icon-spin {
  -webkit-animation: home-spin infinite 3.9s linear;
          animation: home-spin infinite 3.9s linear;
}
@-webkit-keyframes home-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes home-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.aside .nav-wrap .nav-item:hover {
  background-color: rgba(0, 0, 0, 0.2);
}
.aside .nav-wrap .nav-icon {
  margin-right: 10px;
  font-size: 26px;
}
.aside .nav-wrap .nav-item-active {
  background-color: rgba(0, 0, 0, 0.2);
}
.aside .aside-footer {
  width: 100%;
  position: absolute;
  bottom: 0;
  font-size: 12px;
}
.aside .aside-footer .aside-wrap {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px 0 10px;
}
.aside .aside-footer .aside-wrap .cat-paw {
  width: 50px;
  height: 50px;
  margin-bottom: 10px;
  position: relative;
}
.aside .aside-footer .aside-wrap .cat-paw .outer-ring {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-animation: home-spin 10s infinite linear;
          animation: home-spin 10s infinite linear;
}
.aside .aside-footer .aside-wrap .cat-paw .outer-ring [class^='item'] {
  width: 100%;
  height: 4px;
  position: absolute;
  top: 24px;
  left: 0;
}
.aside .aside-footer .aside-wrap .cat-paw .outer-ring [class^='item']:after {
  content: '';
  width: 4px;
  height: 4px;
  background: #fff;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  border-radius: 50%;
}
.aside .aside-footer .aside-wrap .cat-paw .outer-ring .item-1 {
  -webkit-transform: rotate(30deg);
          transform: rotate(30deg);
}
.aside .aside-footer .aside-wrap .cat-paw .outer-ring .item-2 {
  -webkit-transform: rotate(60deg);
          transform: rotate(60deg);
}
.aside .aside-footer .aside-wrap .cat-paw .outer-ring .item-3 {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.aside .aside-footer .aside-wrap .cat-paw .outer-ring .item-4 {
  -webkit-transform: rotate(120deg);
          transform: rotate(120deg);
}
.aside .aside-footer .aside-wrap .cat-paw .outer-ring .item-5 {
  -webkit-transform: rotate(150deg);
          transform: rotate(150deg);
}
.aside .aside-footer .aside-wrap .cat-paw .outer-ring .item-6 {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.aside .aside-footer .aside-wrap .cat-paw .outer-ring .item-7 {
  -webkit-transform: rotate(210deg);
          transform: rotate(210deg);
}
.aside .aside-footer .aside-wrap .cat-paw .outer-ring .item-8 {
  -webkit-transform: rotate(240deg);
          transform: rotate(240deg);
}
.aside .aside-footer .aside-wrap .cat-paw .outer-ring .item-9 {
  -webkit-transform: rotate(270deg);
          transform: rotate(270deg);
}
.aside .aside-footer .aside-wrap .cat-paw .outer-ring .item-10 {
  -webkit-transform: rotate(300deg);
          transform: rotate(300deg);
}
.aside .aside-footer .aside-wrap .cat-paw .outer-ring .item-11 {
  -webkit-transform: rotate(330deg);
          transform: rotate(330deg);
}
.aside .aside-footer .aside-wrap .cat-paw .outer-ring .item-12 {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
}
.aside .aside-footer .aside-wrap .cat {
  width: 100%;
  height: 100%;
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.aside .aside-footer .aside-wrap .time {
  letter-spacing: 2px;
}
.aside .aside-footer a {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 40px;
  color: hsla(0, 0%, 100%, 0.7);
}
.aside .aside-footer a i {
  font-size: 16px;
}
.aside .aside-music {
  width: 100%;
  position: absolute;
  bottom: 0;
  display: flex;
  flex-direction: column;
}
.aside .aside-music i {
  position: relative;
  width: 100%;
  height: 60px;
  line-height: 60px;
  text-align: center;
  font-size: 30px;
  opacity: 0.7;
}
.aside .aside-music .antIconUnder,
.aside .aside-music .antIconOn {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.aside .aside-music i:hover {
  background: rgba(0, 0, 0, 0.2);
  opacity: 1;
}
.container .min-aside {
  width: 60px !important;
  padding-top: 20px;
}
.container .min-aside .nav-item {
  padding-left: 0;
  justify-content: center;
}
.container .min-aside .aside-wrap img {
  width: 40px;
  height: 40px;
}
.container .min-aside .nav-icon {
  margin-right: 0;
}
